

import Slide from "components/Index/Slider.vue";
import {getFirstScreenData} from "api/api_recommend.js"
export default {
components: {
Slide
},
data() {
return {





loop: true,
autoPlay: true,
picArray: [],
videoArray: [],
partTitle: "推荐歌单",
contentArray: []
};
},
methods:{
getAllData() {
let vm = this;
getFirstScreenData(["bannerData"])
.then(
function(res) {

let banner=res[0];



if (banner.data.code == "200") {
vm.picArray = banner.data.banners;
}









})
;
}
},
mounted(){
this.getAllData();
}
};



